import React, { Component, Fragment } from 'react';
import { Container, Draggable } from 'react-smooth-dnd';
import { applyDrag, generateItems } from './utils';
import houseSVG from './house.svg'
import { Button } from '@material-ui/core'
import { withStyles, withTheme } from "@material-ui/core/styles";
import Typography from 'material-ui/styles/typography';
import bricktextureJFIF from './bricktexture.jfif'
import dollHouseJPG from './doll-house-cartoon-vector.jpg'
import './Attributes.css'
import $ from 'jquery'
import {Route, Link, Redirect } from 'react-router-dom';
var clip_percentage = 100
const groupStyle = {
  marginLeft: '50px',
  flex: 1,
  // backgroundColor:'blue',
  fontSize: '15pt',
  // color : 'white'
};

const styles = theme => ({
  important_attributes: {
    borderRadius : '100px'
  },
  houseSVG: {
    maxWidth: '75vw',
    position: 'absolute',
    left : '10vw',
    top : '25vh',
    maxHeight : '30vh',
    transform: 'scaleX(-1)',
    webkitTransform: 'scaleX(-1)',
  },
  chooseItemContainer: {
    // backgroundColor : 'yellow',
    minWidth: '50vw',
    position: 'fixed',
    left : '15vw',
    bottom: '21vh'
  }
  ,
  imgContainer: {
    position: 'relative'
  },


  homeDropContainer: {
    backgroundColor : 'rgba(220,220,222,0.5)',
    overflow: 'visible',
    position: 'fixed',
    top: '15vh',
    right: '10vw',
    // minHeight: '35vh',
    textAlign: 'right',
    padding : '25px 5px 25px 5px',
    maxWidth: '85vw',
    // backgroundImage: `url(${houseSVG}) `,
    // backgroundImage: 'no-repeat'
  },
  dragItems: {
    // backgroundColor : 'rgb(3,182,252)',
    // color : 'white',

  },
  dollHouse: {
    maxWidth: '100vw',
    position: 'absolute',
    top: '25vh',
    clip: `rect(0px, 600px, 000px, 0)`,
    transform: 'scaleX(-1)',
    webkitTransform: 'scaleX(-1)',
  },
  FinishBtn : {
    position : 'fixed',
    bottom : '15vh',
    left : '39vw'
  },
  root : {
    backgroundColor : 'rgb(3,144,252)',
    maxHeight : '100vh'
  }

})








// const useStyles = makeStyles((theme) => ({

// })

class Groups extends Component {
  constructor(props) {
    super();

    this.state = {
      items1: [{ id: '1', data: 'Energetic & Lively', }, { id: '2', data: 'Nearby Nature' }, { id: '3', data: 'Cozy & Comfy' }, { id: '4', data: 'Transit Central' }],
      items2: [{ id: '5', data: 'Quiet', }],
      clip_percentage: 0
    };
  }
  handleDrop(e) {
    {
      this.setState(prevState => ({ items1: applyDrag(this.state.items1, e), clip_percentage: prevState.clip_percentage + 3 }));
      $('#dollHouse').css('clip', `rect(0px, 600px, ${parseInt((this.state.clip_percentage) * 25)}px, 0)`)
    }
  }

  handleFinish(){
    console.log('Handle Click')
    console.log(document.getElementById('important_attributes'))
    // this.setState({finishRedirectBool : true})
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <h2>Which attributes matter most to you in a home?</h2>
        <p>Select by order of importance</p>
        <div className={classes.imgContainer}>
          {/* <img src={houseSVG} className={classes.houseSVG} /> */}
          <img src={dollHouseJPG} className={classes.dollHouse} id='dollHouse' />
        </div>


        <div style={{ display: 'flex', justifyContent: 'stretch', marginTop: '50px', marginRight: '50px' }} className={classes.dragBox}>

          {/* <div></div>   */}
          <div className={classes.chooseItemContainer}>
            <Container groupName="1" getChildPayload={i => this.state.items1[i]} onDrop={e => this.handleDrop(e)} >
              {
                this.state.items1.map(p => {
                  return (
                    <Draggable key={p.id}>
                      <div className="attributes_to_choose" variant='contained'>
                        {p.data}
                      </div>
                    </Draggable>
                  );
                })
              }
            </Container>
          </div>
          <div style={groupStyle} id='important_attributes'  className={classes.homeDropContainer}>

            <Container  orientation='verticle' groupName="1" getChildPayload={i => this.state.items2[i]} onDrop={e => this.setState({ items2: applyDrag(this.state.items2, e) })}>
              {
                this.state.items2.map(p => {
                  return (
                    <Draggable key={p.id}>
                      <Button className={classes.important_attributes} variant='contained' >
                        {p.data}
                      </Button>
                    </Draggable>
                  );
                })
              }
            </Container>
          </div>
          <Link style={{ textDecoration: 'none' }}>
            <Button variant='contained' color='secondary' className={classes.FinishBtn} onClick={()=>this.handleFinish()}>
              Finish
            </Button>
          </Link>

        </div>
        {this.state.finishRedirectBool && <Redirect to='/Mapbox' />}
        
      </div>
    );
  }
}

Groups.propTypes = {

};

export default withStyles(styles, { withTheme: true })(Groups);